;EAwlBX;EArlBU;;IAEC,gBAAA;EAulBX;EAplBU;;IAEA,gBAAA;EAslBV;EAnlBU;IACI,gBAAA;EAqlBd;EAjlBM;IACI,iBAAA;IACA,aAAA;EAmlBV;EAjlBU;IACI,WAAA;EAmlBd;EA7kBM;IACE,gBAAA;IACA,aAAA;EA+kBR;EA9kBQ;;;IAGE,sBAAA;IACA,uBAAA;EAglBV;EA9kBU;;;IACE,cAAA;EAklBZ;EA7kBM;IACE,8BAAA;IACA,aAAA;IACA,cAAA;EA+kBR;EAzkBS;IACC,YAAA;EA2kBV;AACF","sourcesContent":["$primary-color: #290404;\r\n$secondary-color: #ECA300;\r\n$tertiary-color: #fff;\r\n$hero-color: #EBD6C6;\r\n$box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1), 0 6px 6px rgba(0, 0, 0, 0.1);\r\n$box-shadow-content: 0 5px 10px rgba(0, 0, 0, 0.1), 0 3px 3px rgba(0, 0, 0, 0.1);\r\n$box-shadow-secondary: -1px 1px 15px 1px rgba(236,163,0,0.71);\r\n\r\n.font-pm,\r\n.clr-pm {\r\n    color: $primary-color;\r\n}\r\n\r\n.font-sc,\r\n.clr-sc {\r\n    color: $secondary-color;\r\n}\r\n\r\n.font-tr,\r\n.clr-tr {\r\n    color: $tertiary-color;\r\n}\r\n\r\n.font-hr,\r\n.clr-hr {\r\n    color: $hero-color;\r\n}\r\n\r\n// Background color gradient left to right\r\n.bg-clr-gr-lr{\r\n    background: linear-gradient(115deg, #290404 0%, #5C0909 100%);    \r\n}\r\n\r\n.bg-clr-gr-mid{\r\n    background: #590909;    \r\n}\r\n\r\n.bg-clr-gr-rl{\r\n    background: linear-gradient(-115deg, #290404 0%, #5C0909 100%);    \r\n}","// Import global styles\r\n@import 'main.scss';\r\n\r\nh1 {\r\n    margin: 171px 0 0 0;\r\n}\r\n\r\n#contact-us-form {\r\n   #contact-us {\r\n    display: flex;\r\n    flex-flow: row wrap;\r\n    gap: 10px;\r\n\r\n        .add-info {\r\n            display: flex;\r\n            flex-flow: column nowrap;\r\n            line-height: 28px;\r\n            align-items: flex-start;\r\n            position: relative;\r\n            width: 1200px;\r\n            padding: 5px 10px;\r\n            box-shadow: $box-shadow-content;\r\n            border-radius: 10px;\r\n            background-color: $tertiary-color;\r\n        }\r\n    \r\n        .first-name,\r\n        .last-name,\r\n        .email,\r\n        .phone-number {\r\n        display: flex;\r\n        flex-flow: column nowrap;\r\n        line-height: 28px;\r\n        align-items: flex-start;\r\n        position: relative;\r\n        width: 595px;\r\n        height: 70px;\r\n        padding: 5px 10px;\r\n        box-shadow: $box-shadow-content;\r\n        border-radius: 10px;\r\n        background-color: $tertiary-color;\r\n        }\r\n        \r\n        .first-name-head,\r\n        .last-name-head,\r\n        .email-head,\r\n        .phone-number-head,\r\n        .add-info-head {\r\n            display: flex;\r\n            flex-flow: row nowrap;\r\n            align-items:  center;\r\n            column-gap: 10px;\r\n        }\r\n    \r\n        .first-name-body,\r\n        .last-name-body,\r\n        .email-body,\r\n        .phone-number-body,\r\n        .add-info-body {\r\n            display: flex;\r\n            flex-flow: row nowrap;\r\n            width: 100%;\r\n        }\r\n    \r\n        select,\r\n        select:active,\r\n        input,\r\n        input:active {\r\n        border: none;\r\n        outline: none;\r\n        text-align: left;\r\n        font-size: 1rem;\r\n        width: 100%;\r\n        max-width: 595px;\r\n        min-width: 225px;\r\n        height: 24px;\r\n        }\r\n    \r\n        select:focus,\r\n        input:focus {\r\n        border-color: transparent;\r\n        border-bottom: 2px solid $secondary-color;\r\n        width: 100%;\r\n        max-width: 595px;\r\n        }\r\n    \r\n        textarea,\r\n        textarea:active {\r\n        border: none;\r\n        outline: none;\r\n        text-align: left;\r\n        font-size: 1rem;\r\n        width: 100%;\r\n        max-width: 1200px;\r\n        min-width: 225px;\r\n    \r\n        }\r\n    \r\n        textarea:focus {\r\n            border-color: transparent;\r\n            border-bottom: 2px solid $secondary-color;\r\n            width: 100%;\r\n            max-width: 1200px;\r\n        }\r\n    }\r\n\r\n    #submit-contact {\r\n        display: flex;\r\n        flex-flow: row wrap;\r\n        justify-content: space-between;\r\n        margin-top: 10px;\r\n        \r\n\r\n        .policy-accept {\r\n            display: flex;\r\n            flex-flow: row nowrap;\r\n            align-items: center;\r\n            height: 50px;\r\n            padding: 5px 20px;\r\n            box-shadow: $box-shadow-content;\r\n            border-radius: 10px;\r\n            background-color: $tertiary-color;\r\n\r\n            label {\r\n                margin-left: 10px;\r\n            }\r\n\r\n            input {\r\n             width: 18px;\r\n             height: 18px;\r\n             -webkit-appearance: auto;\r\n             appearance: auto;   \r\n            }\r\n        }\r\n\r\n        button,\r\n        button:link,\r\n        button:visited {\r\n            width: 170px;\r\n            height: 50px;\r\n            padding: 5px 20px;\r\n            color: #000;\r\n            background-color: $tertiary-color;\r\n            border: none;\r\n            border-radius: 10px;\r\n            box-shadow: $box-shadow-content;\r\n        }\r\n\r\n        button:hover {\r\n            color: $tertiary-color;\r\n            background-color: $secondary-color;\r\n        }\r\n\r\n        button:active {\r\n            color: $tertiary-color;\r\n            background-color: $secondary-color;\r\n            transform: translateY(2px)\r\n        }\r\n    }\r\n\r\n      /* The Modal (background) */\r\n  .modal {\r\n    display: none; /* Hidden by default */\r\n    position: fixed; /* Stay in place */\r\n    z-index: 1; /* Sit on top */\r\n    padding-top: 100px; /* Location of the box */\r\n    left: 0;\r\n    top: 0;\r\n    width: 100%; /* Full width */\r\n    height: 100%; /* Full height */\r\n    overflow: auto; /* Enable scroll if needed */\r\n    background-color: rgb(0,0,0); /* Fallback color */\r\n    background-color: rgba(0,0,0,0.4); /* Black w/ opacity */\r\n    z-index: 10;\r\n  }\r\n  \r\n  /* Modal Content */\r\n  .modal-content {\r\n    position: relative;\r\n    background-color: $primary-color;\r\n    margin: auto;\r\n    padding: 0;\r\n    width: 80%;\r\n    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2),0 6px 20px 0 rgba(0,0,0,0.19);\r\n    -webkit-animation-name: animatetop;\r\n    -webkit-animation-duration: 0.4s;\r\n    animation-name: animatetop;\r\n    animation-duration: 0.4s\r\n  }\r\n  \r\n  /* Add Animation */\r\n  @-webkit-keyframes animatetop {\r\n    from {top:-300px; opacity:0} \r\n    to {top:0; opacity:1}\r\n  }\r\n  \r\n  @keyframes animatetop {\r\n    from {top:-300px; opacity:0}\r\n    to {top:0; opacity:1}\r\n  }\r\n  \r\n  /* The Close Button */\r\n  .close {\r\n    color: $tertiary-color;\r\n    float: right;\r\n    font-size: 32px;\r\n  }\r\n  \r\n  .close:hover,\r\n  .close:focus {\r\n    color: $secondary-color;\r\n    text-decoration: none;\r\n    cursor: pointer;\r\n  }\r\n  \r\n  .modal-header {\r\n    padding: 2px 16px;\r\n    background-color: $primary-color;\r\n    color: $tertiary-color;\r\n  }\r\n  \r\n  .modal-body {\r\n    padding: 20px 16px;\r\n  }\r\n  \r\n  .modal-footer {\r\n    padding: 20px 16px;\r\n    background-color: $primary-color;\r\n    color: $tertiary-color;\r\n    display: flex;\r\n    flex-flow: column wrap;\r\n    justify-content: center;\r\n    align-items: center;\r\n  }\r\n}\r\n\r\n#contact-us-card-contact-page {\r\n    display: flex;\r\n    flex-flow: column wrap;\r\n    background-color: $secondary-color;\r\n    margin-top: 20px;\r\n    border-radius: 10px;\r\n  \r\n    .contact-info {\r\n      display: flex;\r\n      flex-flow: row wrap;\r\n      justify-content: space-evenly;\r\n  \r\n      .ci-phone,\r\n      .ci-mail,\r\n      .ci-location {\r\n        display: flex;\r\n        flex-flow: row nowrap;\r\n        column-gap: 10px;\r\n        align-items: center;\r\n    \r\n        .icn-phone {\r\n          transform: scaleX(-1);\r\n          -moz-transform: scaleX(-1);\r\n          -webkit-transform: scaleX(-1);\r\n          -ms-transform: scaleX(-1);\r\n        }\r\n      }\r\n    }\r\n    \r\n    .cc-social {\r\n      display: flex;\r\n      flex-flow: row nowrap;\r\n      justify-content: center;\r\n      align-items: center;\r\n      column-gap: 20px;\r\n    }\r\n}\r\n\r\n#our-branches {\r\n    display: flex;\r\n    flex-flow: column wrap;\r\n    margin-bottom: 100px;\r\n\r\n    h2 {\r\n        margin: 0;\r\n    }\r\n\r\n    hr {\r\n        width: 47%;\r\n        align-content: flex-start;\r\n        border: none;\r\n        background-color: $primary-color;\r\n        height: 5px;\r\n        margin: 20px 0;\r\n    }\r\n\r\n    .branches {\r\n        display: flex;\r\n        flex-flow: row wrap;\r\n        gap: 20px 12px;\r\n        justify-content: space-between;\r\n        align-items: center;\r\n\r\n        .branch {\r\n            display: flex;\r\n            flex-flow: column wrap;\r\n            max-width: 277px;\r\n\r\n            .branch-phone,\r\n            .branch-email,\r\n            .branch-location {\r\n                display: flex;\r\n                flex-flow: row nowrap;\r\n                align-items: center;\r\n                column-gap: 10px;\r\n            }\r\n        }\r\n\r\n        .vertical-line {\r\n            width: 2px;\r\n            height: 220px;\r\n            background-color: $primary-color;\r\n        }\r\n    }\r\n}\r\n\r\n\r\n\r\n// Media Queries\r\n@media only screen and (max-width: 1210px) {\r\n    #contact-us-form {\r\n        #contact-us {\r\n            .first-name,\r\n            .last-name,\r\n            .email,\r\n            .phone-number {\r\n             width: 495px;\r\n            }\r\n             \r\n            select,\r\n            select:active,\r\n            input,\r\n            input:active {\r\n             max-width: 495px;\r\n            }\r\n         \r\n            select:focus,\r\n            input:focus {\r\n             max-width: 495px;\r\n            }\r\n\r\n            textarea,\r\n            textarea:active {\